import React from 'react';
import { Link } from 'react-router-dom';
import './index.scss';

export default class Goals extends React.Component {
    state = {
        list: [
            { avatar: "guy_avatar", name: " ", content: "   \" iPhone Xs\"   1 !    !", image: "iPhone_XS_white" },
            { avatar: "guy_avatar", name: " ", content: "  \" \"!  !", image: "first_goal" },
            { avatar: "woman_avatar", name: " ", content: "   \" MacBook Pro\"  3 ! !", image: "mac_book" }
        ]
    }

    render() {
        return(
            <div>
                <Link className="back" to="/" ></Link>
                <h1 className="label"></h1>

                <div className="news-tiles">
                    {
                        this.state.list.map( (item, i) => {
                            return (
                                <div key={i} className="item">
                                    <div className="content">
                                        <div className="item-header">
                                            <img className="avatar" src={ require(`../../assets/${item.avatar}.png`) } alt={item.avatar} />
                                            <div className="name">{ item.name }</div>
                                        </div>
                                        <div className="text">{ item.content }</div>
                                        <button className="more-button"></button>
                                    </div>
                                    <img className="background" src={ require(`../../assets/${item.image}.png`) } alt="background"/>
                                </div>
                            )
                        })
                    }
                </div>
            </div>
        )
    }
}